.booking {
  background-color: #b7dafa;
  background-image: url("../img/booking-background.jpg");
  background-size: cover;
  width: 100vw;
  min-width: $vp-1024;

  @media (min-width: $vp-1024) {
    @media (min-resolution: 2dppx) {
      background-image: url("../img/booking-background@2x.jpg");
    }
  }

  @media (max-width: $vp-1023) {
    min-width: 768px;
    background-image: url("../img/tablet/booking-background.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/tablet/booking-background@2x.jpg");
    }
  }

  @media (max-width: $vp-767) {
    min-width: 320px;
    background-image: url("../img/mobile/booking-background.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/mobile/booking-background@2x.jpg");
    }
  }
}

.booking > div {
  padding: 50px 7.5%;
  display: flex;
  flex-wrap: wrap;
  max-width: 1366px;
  margin: 0 auto;

  @media (min-width: 1367px) {
    padding: 50px 103px 50px 103px;
  }

  @media (max-width: $vp-1023) {
    flex-direction: column;
    padding: 87px 5.8% 100px 5.8%;
    align-items: center;
  }

  @media (max-width: $vp-767) {
    padding: 58px 15px 65px 15px;
  }
}

.booking__text-wrapper {
  padding: 165px 5.2% 142px 5.2%;
  margin: 0 7.2% 0 0;
  width: 52.9%;

  @media (max-width: $vp-1023) {
    padding: 0;
    margin: 0 0 22px 0;
    text-align: center;
  }

  @media (max-width: $vp-767) {
    width: 100%;
    margin: 0 0 32px 0;
  }
}

.booking__text-wrapper h3 {
  font-size: 40px;
  line-height: 60px;
  font-weight: 700;
  margin: 0 0 5px 0;

  @media (max-width: $vp-1023) {
    font-size: 34px;
    line-height: 51px;
    margin: 0 0 5px -30px;
    width: 420px;
  }

  @media (max-width: $vp-767) {
    font-size: 24px;
    line-height: 31px;
    width: 89%;
    max-width: 480px;
    margin: 0 auto 15px auto;
  }
}

.booking__text-wrapper > p {
  font-size: 18px;
  line-height: 27px;
  font-weight: 400;
  margin: 0 0 30px 0;
  width: 350px;

  @media (max-width: $vp-1023) {
    width: 352px;
    margin: 0 auto 30px auto;
  }

  @media (max-width: $vp-767) {
    width: 89%;
    max-width: 480px;
    margin: 0 auto;
    font-size: 16px;
    line-height: 22px;
  }
}

.booking__text-br {
  display: none;

  @media (max-width: $vp-767) {
    display: inline;
  }
}

.booking__form-wrapper {
  background-color: #011c40;
  padding: 45px 4.3% 30px 4.3%;
  width: 34.7%;
  min-width: 345px;

  @media (min-width: 1367px) {
    width: 403px;
  }

  @media (max-width: $vp-1023) {
    width: 59.4%;
    padding: 45px 50px 50px 50px;
  }

  @media (max-width: $vp-767) {
    padding: 35px 30px 40px 30px;
    width: 100%;
    min-width: 290px;
    max-width: 600px;
  }
}

.booking__form-wrapper > p {
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  color: #f9fbfd;
  margin: 0 0 25px 0;

  @media (max-width: $vp-767) {
    line-height: 25px;
    text-align: center;
    margin: 0 0 28px 0;
  }
}

.booking__label-wrapper {
  margin: 0 0 21px 0;
  position: relative;
}

.booking__label-wrapper--agreement {
  margin: -3px 0 27px 0;

  @media (max-width: $vp-767) {
    max-width: 310px;
    margin: -3px 0 20px 0;
  }
}

.booking__form-wrapper fieldset {
  padding: 0;
  margin: 0;
  border: none;
}

.booking__label-wrapper input {
  border: none;
  width: 100%;
  min-width: 220px;
  height: 43px;
  color: rgba(1, 28, 64, 0.7);
  background-color: #f9fbfd;
  padding: 11px 15px;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;

  &:hover {
    background-color: #93d8fa;
  }

  @media (max-width: $vp-767) {
    min-width: 230px;
  }
}

.booking__checkbox-mark {
  display: inline-block;
  vertical-align: baseline;
  position: absolute;
  width: 18px;
  height: 18px;
  background-color: #f9fbfd;
  top: 1px;
  cursor: pointer;
}

.booking__label-wrapper input:focus + .booking__checkbox-mark {
  outline: 2px solid #93d8fa;
}

.booking__label-wrapper:hover .booking__checkbox-mark {
  background-color: #93d8fa;
}

.booking__label-wrapper input:checked + .booking__checkbox-mark {
  &::after {
    content: "";
    display: block;
    width: 5px;
    height: 8px;
    border-bottom: 1px solid #101a56;
    border-right: 1px solid #101a56;
    transform: translate(6px, 4px) rotate(45deg);
  }
}

.booking__checkbox-text {
  color: #f9fbfd;
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  padding: 5px 0 5px 23px;
  cursor: pointer;
  display: block;

  @media (max-width: $vp-767) {
    display: block;
    padding: 0 0 5px 25px;
  }
}

.booking__form-wrapper button {
  display: inline-block;
  vertical-align: baseline;
  padding: 11px 20px;
  width: 100%;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #011c40;
  border: none;
  background-color: #f9fbfd;
  cursor: pointer;
  user-select: none;

  &:hover {
    background-color: #949494;
  }

  &:active {
    background-color: #93d8fa;
  }
}
